body{
    font-family: 'Dancing Script', cursive;
}
h1{
    padding:35px;
}
div{
    width:100%;
    height:100vh;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
button{
    padding: 12px 20px;
    background-color: #31312d;
    border: 2px solid #ecf0f1;
    outline:none;
    border-radius: 5px;
    text-transform: uppercase;
    cursor:pointer;
    font-size: 16px;
    font-family: 'Dancing Script', cursive;
  }